	position: relative;
	margin: 6% auto;
	background: white;
	padding: 5px;
	overflow: hidden;
	border-radius: 30px;
}

.button-box {
	width: 220px;
	margin: 35px auto;
	position: relative;
	box-shadow: 0 0 20px 9px #ff61241f;
	border-radius: 30px;
}

.toggle-btn {
	padding: 10px 30px;
	cursor: pointer;
	background: transparent;
	border: 0;
	outline: none;
	position: relative;
}

button:hover {
	opacity: 0.5;
}

#btn {
	top: 0;
	left: 0;
	position: absolute;
	width: 110px;
	height: 100%;
	background: linear-gradient(to right, #ff105f, #ffad06);
	border-radius: 30px;
	transition: .20s;
}

.input-group {
	top: 180px;
	position: absolute;
	width: 280px;
	transition: .20s;
}

.input-field {
	width: 100%;
	padding: 10px 0;
	margin: 5px 0;
	border-left: 0;
	border-top: 0;
	border-right: 0;
	border-bottom: 1px solid #999;
	outline: none;
	background: transparent;
}

.submit-btn {
	width: 85%;
	padding: 10px 30px;
	cursor: pointer;
	display: block;
	margin: 30px;
	background: linear-gradient(to right, #ff105f, #ffad06);
	border: 0;
	outline: none;
	border-radius: 30px;
}

.check-box {
	margin: 30px 10px 30px 0;
}

.remember {
	color: #777;
	font-size: 12px;
	padding: 5px;
	bottom: 89px;
	position: absolute;
}

#login {
	left: 50px
}

#register {
	left: 50px;
}

.create-exam-form {
	display: flex;
	flex-direction: column;
}

.create-exam-form input {
	width: 15rem;
}

.create-exam-form button {
