}

.wrapper {
	width: 1170px;
	margin: auto;
}

.home-header {
	background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(https://www.ice.gov/sites/default/files/2021-01/SEVP.jpg);
	height: 100vh;
	background-size: cover;
	background-position: center center;
	position: relative;
}


.nav-area li {
	display: inline-block;
}

.nav-area li {
	color: #fff;
	text-decoration: none;
	padding: 5px 20px;
	font-family: poppins;
	font-size: 16px;
	text-transform: uppercase;
}

.nav-area li:hover {
	background: #fff;
	color: #333;
}

.logo {
	float: left;
}

.logo img {
	width: 25%;
	padding: 15px 0;
	opacity: 0.85;
	border-radius: 50px 50px;
}

.welcome-text {
	position: absolute;
	width: 600px;
	height: 300px;
	margin: 20% 30%;
	text-align: center;
}

.welcome-text h1 {
	text-align: center;
	color: #fff;
	text-transform: uppercase;
	font-size: 60px;
}

.welcome-text h1 span {
	color: #00fecb;
}

.welcome-text a {
	border: 1px solid #fff;
	padding: 10px 25px;
	text-decoration: none;
	text-transform: uppercase;
	font-size: 14px;
	margin-top: 20px;
	display: inline-block;
	color: #fff;
}

.welcome-text a:hover {
	background: #fff;
	color: #333;
}

